import React from 'react';

const Tips = () => (
  <div style={{textAlign: 'left', margin: '30px', width: '800px'}}>
    <h2>Fr du bruker applikasjonen</h2>
    <br/>
    <h4>Cross-Origin Resource Sharing</h4>
    <p>JIRA pner ikke for CORS p REST-kall ifra ukjente domener, som gjr at du m deaktivere sikkerhetsmekanismer i nettleseren din for  bruke applikasjonen (inntil videre). Gitt at du bruker Chome p et Linux-basert system:</p>
    <p>&nbsp;&nbsp; Srg for at du ikke har noen instanser av Chrome som kjrer p maskinen.</p>
    <p>&nbsp;&nbsp; Start chrome med flaggene <code>--disable-web-security</code> og <code>--user-data-dir</code> aktivert.</p>
    <p>e.g. <code>/opt/google/chrome/google-chrome --disable-web-security --user-data-dir</code></p>
    <p>Jeg ville forsvidt ikke anbefalt  surfe for mye p internett i denne tilstanden.</p>
    <br/>
    <h4>Login</h4>
    <p>Login krever samme passord of brukernavn som JIRA brukeren din. Om du mter problemer med  logge inn kan det hende at CAPTCHA-systemet til JIRA har blitt aktivert, i hvilket tilfelle er du ndt til  beske JIRA og (logge ut og) logge inn, da Atlassian ikke tilbyr noe bedre alternativ.</p>
    <br/>
    <h4>Feedback</h4>
    <p>Om du har noe p hjertet kan du kontakte meg via <i>Martin.Jonassen@skatteetaten.no</i></p>
  </div>
);

export default Tips;


